.extra {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    top: 15px;
    display: block;
    width: 100%;
    height: 3px;

    background-color: #68b738;
  }
}

.extra__title-wrapper {
  width: 320px;
  margin: 0 auto;
  margin-bottom: 33px;
  display: flex;
  align-items: center;
}

.extra__title {
  margin: 0;
  padding-left: 20px;
  padding-right: 24px;

  background-color: white;

  font-weight: normal;
  font-size: 24px;
  line-height: 24px;
  text-transform: none;
}

.extra__order-all {
  box-sizing: border-box;
  width: 280px;
  min-height: 289px;
  padding-left: 60px;
  padding-right: 60px;
  padding-bottom: 43px;
  margin-top: 39px;
  margin-bottom: 35px;
  margin-left: auto;
  margin-right: auto;
  display: flex;

  text-align: center;

  background-color: #68b738;
  background-image: url("../img/icon-gift.svg"),
    url("../img/gift-photo-mobile@1x.jpg");
  background-repeat: no-repeat, no-repeat;
  background-position: center top 65px, center;
  background-size: 56px auto, 280px auto;

  @media (min-resolution: $retina-dpi),
    (min-resolution: $retina-dppx) {
    background-image: url("../img/icon-gift.svg"),
      url("../img/gift-photo-mobile@2x.jpg");
    background-repeat: no-repeat, no-repeat;
    background-position: center top 65px, center;
    background-size: 56px auto, 280px auto;
  }
}

.extra__text {
  margin-top: auto;

  @include arial-tablet;

  line-height: 20px;
  color: white;
}

@media (min-width: $tablet-width) {
  .extra {
    margin-top: 9px;

    &::after {
      top: 21px;
    }
  }

  .extra__title-wrapper {
    width: 708px;
    margin: 0 auto;
    margin-bottom: 79px;
  }

  .extra__title {
    padding-left: 40px;
    padding-right: 40px;

    font-size: 36px;
    line-height: 36px;
  }

  .extra__order-all {
    width: 708px;
    min-height: 200px;
    margin-top: 69px;
    margin-bottom: 76px;
    padding-left: 423px;
    padding-right: 125px;
    padding-top: 72px;
    padding-bottom: 0;

    background-image: url("../img/icon-gift.svg"),
      url("../img/gift-photo-tablet@1x.jpg");
    background-repeat: no-repeat, no-repeat;
    background-position: center left 91px, center;
    background-size: 56px auto, 708px auto;

    @media (min-resolution: $retina-dpi),
      (min-resolution: $retina-dppx) {
      background-image: url("../img/icon-gift.svg"),
        url("../img/gift-photo-tablet@2x.jpg");
      background-repeat: no-repeat, no-repeat;
      background-position: center left 91px, center;
      background-size: 56px auto, 708px auto;
    }
  }

  .extra__text {
    margin: 0;

    text-align: left;
  }
}

@media (min-width: $desktop-width) {
  .extra {
    margin-top: 18px;
    margin-bottom: 86px;
  }

  .extra__title-wrapper {
    width: 1300px;
  }

  .extra__wrapper {
    width: 1220px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  .extra__catalog {
    width: 895px;
  }

  .extra__order-all {
    width: 245px;
    min-height: 289px;
    margin: 0;
    padding-left: 0;
    padding-right: 0;
    padding-top: 187px;
    padding-bottom: 0;
    justify-content: center;

    background-image: url("../img/icon-gift.svg"),
      url("../img/gift-photo-desktop@1x.jpg");
    background-repeat: no-repeat, no-repeat;
    background-position: center top 65px, center;
    background-size: 56px auto, 245px auto;

    @media (min-resolution: $retina-dpi),
      (min-resolution: $retina-dppx) {
      background-image: url("../img/icon-gift.svg"),
        url("../img/gift-photo-desktop@2x.jpg");
      background-repeat: no-repeat, no-repeat;
      background-position: center top 65px, center;
      background-size: 56px auto, 245px auto;
    }
  }

  .extra__text {
    width: 70%;
    text-align: center;
  }
}
